* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body 
{
    background: white;
}
a
{
    color: unset;
    text-decoration: none;
}
/****************************** For Graidient text and background Start ******************************/

.gradient_bg
{
        /* ff 3.6+ */
        background:-moz-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* safari 5.1+,chrome 10+ */
        background:-webkit-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* opera 11.10+ */
        background:-o-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* ie 10+ */
        background:-ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* global 92%+ browsers support */
        background:radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 100%);       
}
.gradient_text
{
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

/****************************** For Graidient text and background End ******************************/

/****************************** For text padding & margin start ******************************/
.section_padding 
{
    padding: 4rem 3rem;
}
.section_margin
{
    margin: 4rem 6rem;
}
/****************************** For text padding & margin start ******************************/

/****************************** From animista Navbar mobile effect start ******************************/
.scale-up-center {
	-webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
	        animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

 @-webkit-keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  @keyframes scale-up-center {
    0% {
      -webkit-transform: scale(0.5);
              transform: scale(0.5);
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }

/****************************** From animista Navbar mobile effect  end ******************************/

@media screen and (max-widt: 700px)
{
    .section_padding
    {
        padding: 4rem;
    }
    .section_margin
    {
        margin: 4rem;
    }
}
@media screen and (max-widt: 550px)
{
    .section_padding
    {
        padding: 4rem 2rem;
    }
    .section_margin
    {
        margin: 4rem 2rem;
    }
}
